import c from '@/static/mbcImg/my/c.png';
import yi from '@/static/mbcImg/my/yi.png';
import shag from '@/static/mbcImg/my/shag.png';
import xia from '@/static/mbcImg/my/xia.png';
import kong from '@/static/mbcImg/my/kong.png';
import you from '@/static/mbcImg/my/xiajiao.png';
//看過 電話 聊天 點讚..
import zan from '@/static/mbcImg/my/zan.png';
import look from '@/static/mbcImg/my/look.png';
import phone from '@/static/mbcImg/my/phone.png';
import liu from '@/static/mbcImg/my/liu.png';
import shou from '@/static/mbcImg/my/shou.png';
import fen from '@/static/mbcImg/my/fen.png';
//結束
import { mapMutations, mapGetters } from 'vuex';
export default {
	data() {
		return {
			c:c,
			yi:yi,
			shag:shag,
			xia:xia,
			zan:zan,
			look:look,
			phone:phone,
			liu:liu,
			shou:shou,
			fen:fen,
			kong:kong,
			titles:['全部','已发布','待发布','已下架'],
			nums:0, //0为全部 1为已发布 2为待发布 3为已下架
			hiden:0, //0为隐藏 1为显示
			you:you,
			tap:0, //控制事件重复触发
			message:'全部',
			coloe:'0',
			filterProducts:[],
			filte:[],
			filter:[],
			list:[],
			list2:[],
			list3:[],
			list4:[],
			shu:[],
		};
	},
	components: {
		
	},
	computed: {
		
	  ...mapGetters(['GET_HOME', 'QUICKNAVCO','GET_MY'])
	},
	watch: {
		GET_MY: {
			handler(a, b) {
				console.log(a, b)
				this.shu = a.zi;
				console.log(this.shu)
			},
			deep: true
		},
	
	},
	created() {
		this.inf();
	},
	mounted(){
		this.list=this.GET_MY.zi
		this.list2=this.GET_MY.zi
		this.list3=this.GET_MY.zi
		this.list4=this.GET_MY.zi
	},
	methods: {
		inf(){//资讯
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/instruc?userId=760&page=1&instrucCustType=2', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.zi = response.data.content.list
						this.$store.commit('setzi', this.zi);
						// this.lists = response.data.content.instKeys
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		gotomyproducts(e){
			console.log('to说明书详情')
			uni.navigateTo({
				url: '/modules/InstructionsPrice/myproduct/product-book/product-books?id='+e,
			});
		},
		change(index){
			this.nums = index;
			if(index === 0){
				this.message = '全部';
				this.coloe='1';
				this.hiden = 0;
				// this.list=this.GET_MY.product
				// console.log(this.list)
				this.$store.commit('setzi', this.list);
			}else if (index === 1){
				this.message = '已发布';
				this.coloe='1';
				this.hiden = 0;
				
				for(let i = 0;i<this.list2.length;i++){
					if(this.list2[i].instrucState == 1){
						this.filterProducts.push(this.list2[i]);
					}
				}
				this.$store.commit('setzi', this.filterProducts);
			}else if (index === 2){
				this.message = '待发布';
				this.coloe='1';
				this.hiden = 0;
				
				for(let a = 0;a<this.list3.length;a++){
					if(this.list3[a].instrucState == 0){
						this.filte.push(this.list3[a]);
					}
				}
				this.$store.commit('setzi', this.filte);
				console.log(this.filte)
			}else if (index === 3){
				this.message = '已下架';
				this.coloe='1';
				this.hiden = 0;
				
				for(let i = 0;i<this.list4.length;i++){
					if(this.list4[i].instrucState == 2){
						this.filter.push(this.list4[i]);
					}
				}
				this.$store.commit('setzi', this.filter);
			}
			
		},
		hides(){
			if(this.tap === 0){
				this.hiden = 1;
				this.tap = 1;
			}else if(this.tap === 1){
				this.hiden = 0;
				this.tap = 0;
			}
			
		}
	}
};
